@custom-media --wide-vp (width >= 1024px);
@custom-media --not-wide-vp (width <= 1023.9px);
@custom-media --middle-vp (768px <= width < 1023.9px);
@custom-media --small-vp (width <= 767.9px);
@custom-media --not-small-vp (width >= 768px);
@custom-media --x2 (min-resolution: 2dppx);
html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
img {
  max-width: 100%;
  height: 100%;
  object-fit: cover;
}
a {
  text-decoration: none;
}
@font-face {
  font-family: "PT Mono";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src:
    url("../fonts/ptmono-regular.woff2") format("woff2"),
    url("../fonts/ptmono-regular.woff") format("woff");
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src:
    url("../fonts/roboto-medium.woff2") format("woff2"),
    url("../fonts/roboto-medium.woff") format("woff");
}
@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src:
    url("../fonts/roboto-condensed.woff2") format("woff2"),
    url("../fonts/roboto-condensed.woff") format("woff");
}
@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src:
    url("../fonts/roboto-condensed-bold.woff2") format("woff2"),
    url("../fonts/roboto-condensed-bold.woff") format("woff");
}
:root {
  --regular-font: "PT Mono", "Courier New", monospace;
  --accent-font: "Roboto Condensed", "Arial", sans-serif;
  --medium-font: "Roboto", "Arial", sans-serif;
}
:root {
  --color-white: #ffffff;
  --color-base-light: #f2f5f6;
  --color-base-dark: #041827;
  --color-accent: #00b2ff;
  --color-bg-base: #f8fdff;
  --color-bg-dark: #041827;
  --color-bg-screen: #37648e;
  --color-border: #ebebeb;
  --color-gradient-header: rgba(4, 24, 39, 0);
  --color-gradient-image-clear: rgba(25, 32, 36, 0.7);
  --color-gradient-image: rgba(25, 32, 36, 0);
}
.page {
  height: 100%;
  font-family: var(--regular-font);
  font-weight: 400;
  color: var(--color-base-dark);
  scroll-behavior: smooth;
}
.page h1 {
  margin-top: 0;
  font-family: var(--accent-font);
  font-style: normal;
  font-weight: 700;
  font-size: 80px;
  line-height: 94px;
  letter-spacing: 0.07em;
  text-transform: uppercase;
  color: var(--color-base-light);

  @media (--middle-vp) {
    font-size: 64px;
    line-height: 75px;
  }

  @media (--small-vp) {
    font-size: 36px;
    line-height: 42px;
  }
}
.page ul {
  list-style-type: none;
  padding-left: 0;
}
.page__body {
  display: flex;
  flex-direction: column;
  width: 100%;
  min-width: 320px;
  min-height: 100%;
  margin: 0;
  background-color: var(--color-bg-base);
}
.page__visual-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page__wrapper-center {
  @media (--wide-vp) {
    max-width: 1220px;
    padding: 0 50px;
    margin: 0 auto;
  }

  @media (--middle-vp) {
    padding: 0 50px;
  }

  @media (--small-vp) {
    padding: 0 25px;
  }
}
.page-main {
  flex-grow: 1;
}
.page-header {
  width: 100%;
  padding-top: 30px;
  padding-bottom: 35px;
  background-image: linear-gradient(180deg, var(--color-base-dark) 84.9%, var(--color-gradient-header) 100%);

  @media (--middle-vp) {
    padding-top: 20px;
    padding-bottom: 25px;
  }

  @media (--small-vp) {
    padding-top: 15px;
    padding-bottom: 20px;
  }

  &--fixe {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 2;
  }
}
.page-header__wrapper-grid {
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
  gap: 20px;

  @media (--not-wide-vp) {
    display: flex;
    justify-content: space-between;
  }
}
.page-header__contacts {
  padding-top: 15px;

  @media (--middle-vp) {
    padding-top: 10px;
  }

  @media (--small-vp) {
    padding-top: 5px;
  }
}
.page-header__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  width: 100%;
  margin: 0;
}
.page-header__link {
  position: relative;
  display: block;
  color: var(--color-base-light);
  font-style: normal;
  font-size: 16px;
  line-height: 18px;
  padding-left: 35px;

  &::before {
    position: absolute;
    content: "";
    display: block;
    mask-repeat: no-repeat;
    mask-position: center;
    background: var(--color-accent);
    width: 20px;
    height: 20px;
    top: 0;
    left: 0;
  }

  &--email::before {
    mask-image: url("../icons/sprite.svg#email");
  }

  &--phone::before {
    mask-image: url("../icons/sprite.svg#phone");
  }
}
.page-header__link span {
  @media (--not-wide-vp) {
    display: none;
  }
}
.page-header__button {
  font-size: 14px;
  line-height: 16px;
  width: 160px;
  height: fit-content;
  min-height: 40px;
  padding: 2px 10px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: var(--color-base-light);
  border: 1px solid var(--color-base-light);
  border-radius: 3px;
  background-color: transparent;

  @media (--not-wide-vp) {
    display: none;
  }
}
.page-footer {
  background-color: var(--color-base-dark);
}
.page-footer em {
  color: var(--color-white);
  font-family: var(--accent-font);
  font-style: normal;
  font-weight: 400;
  font-size: 15px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
}
.page-footer__contacts a,
.page-footer__contacts p,
.page-footer__nav a {
  color: var(--color-white);
  font-style: normal;
  font-size: 16px;
  line-height: 1.35;
  opacity: 70%;
}
.page-footer__contacts a {
  line-height: 25px;
}
.page-footer__container {
  display: flex;
  justify-content: space-between;
}
.button {
  font-family: var(--accent-font);
  font-weight: 700;
  text-decoration: none;
  user-select: none;
}
.logo {
  display: grid;
  width: fit-content;
  grid-template-columns: auto auto;
  grid-template-rows: min-content auto;
  column-gap: 15px;
}
.logo svg {
  width: 55px;
  height: 55px;
  grid-column: 1 / 2;
  grid-row: 1 / -1;

  @media (--small-vp) {
    width: 35px;
    height: 35px;
  }
}
.logo b {
  font-family: var(--accent-font);
  font-style: normal;
  font-weight: 700;
  font-size: 25px;
  line-height: 29px;
  color: var(--color-base-light);
  grid-column: 2 / -1;
  grid-row: 1 / 2;
  padding-top: 5px;
}
.logo i {
  font-family: var(--medium-font);
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 14px;
  color: var(--color-white);
  opacity: 70%;
  grid-column: 2 / -1;
  grid-row: 2 / -1;
  margin-top: -10px;
}
.logo b,
.logo i {
  @media (--small-vp) {
    display: none;
  }
}
.main-nav__list {
  display: grid;
  grid-template-columns: auto auto;
}
.screen {
  position: relative;
  background:
    linear-gradient(180deg, var(--color-gradient-image-clear) 71.72%, var(--color-gradient-image) 100%),
    url("../img/screen-l@1x.jpg") no-repeat;
  background-size: cover;
  background-position: center;
  width: 100%;
  background-color: var(--color-bg-screen);
  margin-top: -120px;
  padding-top: 305px;
  padding-bottom: 280px;
  z-index: -1;

  @media (--x2) {
    background:
      linear-gradient(180deg, var(--color-gradient-image-clear) 71.72%, var(--color-gradient-image) 100%),
      url("../img/screen-l@2x.jpg") no-repeat;
    background-size: cover;
    background-position: center;
  }

  @media (--middle-vp) {
    background:
      linear-gradient(180deg, var(--color-gradient-image-clear) 71.72%, var(--color-gradient-image) 100%),
      url("../img/screen-m@1x.jpg") no-repeat;
    background-size: cover;
    background-position: center;
    margin-top: -100px;
    padding-top: 180px;
    padding-bottom: 205px;

    @media (--x2) {
      background:
        linear-gradient(180deg, var(--color-gradient-image-clear) 71.72%, var(--color-gradient-image) 100%),
        url("../img/screen-m@2x.jpg") no-repeat;
      background-size: cover;
      background-position: center;
    }
  }

  @media (--small-vp) {
    background:
      linear-gradient(180deg, var(--color-gradient-image-clear) 71.72%, var(--color-gradient-image) 100%),
      url("../img/screen-s@1x.jpg") no-repeat;
    background-size: cover;
    background-position: center;
    margin-top: -70px;
    padding-top: 150px;
    padding-bottom: 190px;

    @media (--x2) {
      background:
        linear-gradient(180deg, var(--color-gradient-image-clear) 71.72%, var(--color-gradient-image) 100%),
        url("../img/screen-s@2x.jpg") no-repeat;
      background-size: cover;
      background-position: center;
    }
  }
}
.screen h1 {
  margin-bottom: 40px;
}
.screen i {
  display: block;
  font-style: normal;
  font-size: 22px;
  line-height: 1.55;
  color: var(--color-base-light);
  margin-bottom: 40px;

  @media (--small-vp) {
    font-size: 14px;
  }
}
.screen__button {
  display: grid;
  font-size: 18px;
  line-height: 21px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: var(--color-base-light);
  background-color: var(--color-accent);
  place-items: center;
  width: 410px;
  min-height: 55px;
  border-radius: 3px;

  &::before {
    content: "Получить бесплатную консультацию";
    display: block;
  }

  @media (--small-vp) {
    font-size: 14px;
    line-height: 16px;
    width: 270px;
    min-height: 40px;

    &::before {
      content: "Бесплатная консультация";
    }
  }
}
.advantages {
  padding-top: 120px;
  --hover-color-text: var(--color-base-dark);
}
.advantages__list {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px;
  color: var(--color-accent);
  margin: 0;
}
.advantages__list h3 {
  font-family: var(--accent-font);
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 34px;
  margin-top: 20px;
  margin-bottom: 15px;
}
.advantages__item p {
  color: var(--hover-color-text);
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.35;
  margin-top: 0;
}
.advantages__item {
  position: relative;
  padding: 40px 45px;
  outline: 1px solid var(--color-border);
  outline-offset: -1px;

  &::after {
    content: "";
    position: absolute;
    display: block;
    width: 5px;
    height: 100%;
    background-color: var(--color-accent);
    top: 0;
    left: 0;
  }

  &::before {
    content: "";
    display: block;
    mask-repeat: no-repeat;
    mask-position: center;
    background: currentColor;
    width: 35px;
    height: 35px;

    @media (--small-vp) {
      width: 30px;
      height: 30px;
    }
  }

  &--watch::before {
    mask-image: url("../icons/sprite.svg#watch");
  }

  &--suppliers::before {
    mask-image: url("../icons/sprite.svg#suppliers");
  }

  &--detective::before {
    mask-image: url("../icons/sprite.svg#detective");
  }

  &--tag::before {
    mask-image: url("../icons/sprite.svg#tag");
  }

  &:hover {
    background-color: var(--color-accent);
    outline: none;
    color: var(--color-base-light);
    --hover-color-text: var(--color-base-light);
  }
}
